import { createRoot } from 'react-dom/client';
import { HashRouter, Routes, Route } from "react-router-dom";
import NavBar from './navigationBar/bar.js';
import LightPricesPage from './lightPrizePerHour.js';
import BillPVPC from './billPVPC.js';
import Help from './help.js';

function FacturaLuzApp() {
	return (
		<HashRouter>
			<NavBar />
			<Routes>
				<Route exact path="/" element={<LightPricesPage />} />
				<Route exact path="/facturaPVPC" element={<BillPVPC />} />
				<Route exact path="/help" element={<Help />} />
			</Routes>
		</HashRouter>
	);
}

const root = createRoot(document.getElementById('app'));
root.render(<FacturaLuzApp />);
