import  { NavLink } from "react-router-dom";
import DarkMode from "./darkMode.js"

export function NavItem({ href, label, isSelected }) {
	return (
		<NavLink to={href}
			className={({isActive, isPending}) => isActive ? "w3-bar-item w3-button w3-blue" : "w3-bar-item w3-button"}
			style={({isActive, isPending}) => {
				return {
					fontWeight: isActive ? "bold" : "",
				};
			}}
		>
			{label}
		</NavLink>
	);
}

export default function NavBar() {
	return (
		<>
			<div className="w3-bar w3-border w3-light-grey w3-large">
				<NavItem href="/" label="Precio Luz" />
				<NavItem href="/facturaPVPC" label="Factura PVPC" />
				<NavItem href="/help" label="Ayuda" />
				<DarkMode />
			</div>
			<div className="w3-panel">
				<p></p>
			</div>
		</>
	);
}
