function Accordion({idToShow, children}) {
	function toogleHelp() {
		var x = document.getElementById(idToShow);
		if (x.className.indexOf("w3-show") == -1)
			x.className += " w3-show";
		else
			x.className = x.className.replace(" w3-show", "");
	}
	return (
		<button onClick={toogleHelp} className="w3-button w3-block w3-blue w3-left-align w3-round">{children}</button>
	);
}

