import { useState, useEffect, useRef } from 'react';
import Plot from 'react-plotly.js';
import CountUp from 'react-countup';

function getOffsetDayDate(date, offset) {
	var nextDay = new Date(date);
	if (date)
		nextDay.setDate(date.getDate() + offset);
	return nextDay;
}

function getOffsetMonthDate(date, offset) {
	var nextMonth = new Date(date);
	if (date)
		nextMonth.setMonth(date.getMonth() + offset);
	return nextMonth;
}

function dateToString(date) {
	if (date)
		return date.toISOString().split("T")[0];
	return null;
}

export function LightPricePerHour({startDate, onStartDateChange, endDate, onEndDateChange}) {
	const tomorrow = getOffsetDayDate(new Date(), 1);
	const nextMonth = getOffsetMonthDate(startDate, 1);
	const maxLimit = tomorrow < nextMonth ? tomorrow : nextMonth;
	const prevMonth = getOffsetMonthDate(endDate, -1);
	const minLimit = getOffsetDayDate(prevMonth, 1);
	return (
		<>
			<div className="w3-card-4 w3-round w3-content formBackground" style={{ maxWidth: '490px' }}>
				<div className="w3-container w3-blue w3-round">
					<h1>Precio de la luz por horas</h1>
				</div>
				<h3 className="w3-margin-left">Elige el rango de fechas a consultar</h3>
				<div className="w3-row-padding">
					<div className="w3-half">
						<label>Fecha de inicio</label>
						<input className="w3-input w3-border" type="date" value={dateToString(startDate)} onChange={onStartDateChange} min={dateToString(minLimit)} max={dateToString(endDate)} />
					</div>
					<div className="w3-half">
						<label>Fecha de fin</label>
						<input className="w3-input w3-border" type="date" value={dateToString(endDate)} onChange={onEndDateChange} min={dateToString(startDate)} max={dateToString(maxLimit)} />
					</div>
				</div>
				<div className="w3-margin-left w3-margin-right w3-panel w3-leftbar w3-rightbar w3-border-blue" style={{ backgroundColor: 'rgba(150,255,255,0.2)' }}>
					<label><p>El intervalo mximo es de un mes</p></label>
				</div>
				<div className="w3-padding"></div>
			</div>
			<div className="w3-panel">
				<p></p>
			</div>
		</>
	);
}

export function LightRelevantPrices({ energyPrices }) {
	// TODO: animateValue https://github.com/glennreyes/react-countup
	return (
		<>
			<div className="w3-card-4 w3-round w3-content w3-row w3-center formBackground" style={{ maxWidth: '1100px' }}>
				<div className="w3-third w3-container">
					<h3 className="w3-bottombar w3-border-blue">Precio ms bajo</h3>
					<h1 className="w3-text-green"><CountUp end={energyPrices.minCost} decimals={5} duration={1.0} preserveValue={true} /> /kWh</h1>
					<label>{new Intl.DateTimeFormat('es-ES', { dateStyle: 'full', timeStyle: 'short' }).format(energyPrices.minCostDate)}</label>
				</div>
				<div className="w3-third w3-container">
					<h3 className="w3-bottombar w3-border-blue">Precio medio</h3>
					<h1 className="w3-text-yellow"><CountUp end={energyPrices.meanCosts} decimals={5} duration={1.0} preserveValue={true} /> /kWh</h1>
				</div>
				<div className="w3-third w3-container">
					<h3 className="w3-bottombar w3-border-blue">Precio ms alto</h3>
					<h1 className="w3-text-red"><CountUp end={energyPrices.maxCost} decimals={5} duration={1.0} preserveValue={true} /> /kWh</h1>
					<label>{new Intl.DateTimeFormat('es-ES', { dateStyle: 'full', timeStyle: 'short' }).format(energyPrices.maxCostDate)}</label>
				</div>
				<div className="w3-panel">
					<p></p>
				</div>
				</div>
				<div className="w3-panel">
				<p></p>
			</div>
		</>
	);
}

export function LightPricesChart({ energyPrices }) {
	if (!energyPrices.dates)
		return (<></>);

	const maxChart = energyPrices.maxCost * 1.05;
	const minChart = energyPrices.minCost > 0 ? 0 : energyPrices.minCost;
	const style = window.getComputedStyle(document.body);
	const bgColor = style.getPropertyValue('background-color');
	const textColor = style.getPropertyValue('color');
	const layout = {
		title: {
			text: energyPrices.chartTitle,
			font: {
				color: textColor
			}
		},
		xaxis: {
			title: 'Fecha',
			color: textColor,
			range: [energyPrices.dates[0], energyPrices.dates[energyPrices.dates.length-1]]
		},
		yaxis: {
			title: 'PVPC (/kWh)',
			color: textColor,
			range: [minChart, maxChart]
		},
		plot_bgcolor: bgColor,
		paper_bgcolor: bgColor
	};
	const config = {
		responsive: true,
		modeBarButtonsToRemove: ['select2d', 'lasso2d', 'zoomIn2d', 'zoomOut2d', 'autoScale2d' ]
	};
	const data = [
		{
			x: energyPrices.dates,
			y: energyPrices.energyCosts,
			line: {shape: 'spline'},
			//mode: 'lines+markers',
		}
	];

	return (
		<Plot
			data={data}
			layout={layout}
			config={config}
			className="w3-card-4 w3-content" style={{ maxWidth: '1000px', height: '500px' }}
		/>
	);
}

export default function LightPricesPage() {
	const today = new Date();
	const [startDate, setStartDate] = useState(today);
	const tomorrow = getOffsetDayDate(startDate, 1);
	const [endDate, setEndDate] = useState(tomorrow);
	const [energyPrices, setEnergyPrices] = useState({ minCost: 0, maxCost: 0, meanCosts: 0 });

	useEffect(() => {
		const url = "https://apidatos.ree.es/es/datos/mercados/precios-mercados-tiempo-real?start_date=" + dateToString(startDate) + "T00:00&end_date=" + dateToString(endDate) + "T23:59&time_trunc=hour";
		fetch(url).then(function(response) {
			return response.json();
		}).then(function(jsonData) {
			const valuesJson = jsonData["included"][0]["attributes"]["values"];
			var minCost = 1000;
			var maxCost = -1000;
			var minCostDate = null;
			var maxCostDate = null;
			var sumCosts = 0;
			const dates = valuesJson.map(function(value) {
				return new Date(value.datetime);
			});
			const energyCosts = valuesJson.map(function(value, index) {
				const eurPerkWh = value.value / 1000.0;
				if (eurPerkWh < minCost)
				{
					minCost = eurPerkWh;
					minCostDate = dates[index];
				}
				if (eurPerkWh > maxCost)
				{
					maxCost = eurPerkWh;
					maxCostDate = dates[index];
				}
				sumCosts += eurPerkWh;
				return eurPerkWh;
			});
			setEnergyPrices({ chartTitle: jsonData.data.type, minCost: minCost, maxCost: maxCost, minCostDate: minCostDate, maxCostDate: maxCostDate, meanCosts: sumCosts / energyCosts.length, dates: dates, energyCosts: energyCosts });
		}).catch(function(err) {
			console.error('Ferch Error: ', err);
		});
	}, [startDate, endDate]);


	function onStartDateChange(event) {
		setStartDate(event.target.valueAsDate);
	}
	function onEndDateChange() {
		setEndDate(event.target.valueAsDate);
	}
	return (
		<>
			<LightPricePerHour startDate={startDate} onStartDateChange={onStartDateChange} endDate={endDate} onEndDateChange={onEndDateChange} />
			<LightRelevantPrices energyPrices={energyPrices} />
			<LightPricesChart energyPrices={energyPrices} />
		</>
	);
}
