import { useState } from 'react';

export default function DarkMode() {
	function detectColorScheme() {
		var theme = "light";
		if (localStorage.getItem("theme")) {
			if (localStorage.getItem("theme") == "dark")
				theme = "dark";
		}
		else if (!window.matchMedia)
			return false;
		else if (window.matchMedia("(prefers-color-scheme: dark)").matches)
			theme = "dark";

		if (theme == "dark")
		{
			document.documentElement.setAttribute("data-theme", "dark");
			return true;
		}
		return false;
	}
	const [darkMode, setDarkMode] = useState(detectColorScheme());

	function handleChangeCheckbox(e) {
		if (e.target.checked) {
			localStorage.setItem('theme', 'dark');
			document.documentElement.setAttribute('data-theme', 'dark');
			setDarkMode(true);
		} else {
			localStorage.setItem('theme', 'light');
			document.documentElement.setAttribute('data-theme', 'light');
			setDarkMode(false);
		}
	}
	return (
		<div className="toggle w3-bar-item w3-right w3-medium">
			Modo oscuro
			<input type="checkbox" id="toggleDarkMode" onChange={(e) => handleChangeCheckbox(e)} checked={darkMode} />
			<label className="w3-cell-middle" htmlFor="toggleDarkMode"></label>
		</div>
	);
}